/*------------------------------------*\
    RESET
\*------------------------------------*/
/* http://meyerweb.com/eric/tools/css/reset/ 
    v2.0b1 | 201101 
    NOTE:WORK IN PROGRESS
    USE WITH CAUTION AND TEST WITH ABANDON */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: inherit; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

/* remember to define visible focus styles! 
:focus{
    outline:?????;
} */
/* remember to highlight inserts somehow! */
ins {
  text-decoration: none; }

del {
  text-decoration: line-through; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

#app {
  display: grid;
  grid-template-rows: 108px repeat(2, auto) 539px;
  grid-template-columns: repeat(8, 12.4%);
  grid-column-gap: 1px;
  grid-template-areas: "logo .    .    .    nav  nav  nav  ." "sbar me   me   me   me   me   me   me" "sbar work work work work work work work" "cont cont cont cont cont cont cont cont"; }

.logo-container {
  width: 12%;
  position: fixed;
  grid-area: logo;
  display: flex;
  justify-content: center;
  margin-top: 25px; }

nav {
  margin-top: 25px;
  grid-area: nav;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "me work cont"; }
  nav div {
    grid-area: me; }
  nav div:nth-child(2) {
    grid-area: work; }
  nav div:nth-child(3) {
    grid-area: cont; }

.sidebar {
  grid-area: sbar; }

section {
  grid-template-columns: repeat(7, 1fr);
  align-items: flex-end; }
  section header {
    grid-area: head; }

section#me {
  grid-area: me;
  grid-template-rows: 208px auto;
  grid-template-areas: "head head head head .    .   ." ".    .    .    p    p    p   p"; }
  section#me p {
    grid-area: p;
    margin: 101px 0 86px 0; }

section#work {
  grid-area: work;
  grid-template-areas: "head head head head .    .    ." "p1 p1 p1 p1 p1 p1 p1" "p2 p2 p2 p2 p2 p2 p2" "p3 p3 p3 p3 p3 p3 p3"; }
  section#work header {
    margin-bottom: 109px;
    align-self: flex-end; }
  section#work ul {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(7, 1fr);
    grid-template-areas: "text text . img img img img"; }
    section#work ul .text {
      grid-area: text; }
    section#work ul .image {
      grid-area: img; }
  section#work #project-1 {
    grid-area: p1;
    margin-bottom: 200px; }
  section#work #project-2 {
    grid-area: p2;
    margin-bottom: 200px; }
  section#work #project-3 {
    grid-area: p3;
    margin-bottom: 100px; }

section#contact {
  padding: 50px 0;
  grid-area: cont;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: 185px repeat(2, auto);
  grid-template-areas: ". head head head .    .    . ."  ". p    p    p    p    .    . ." ". foot foot foot foot foot foot .";
  align-items: flex-start; }
  section#contact header {
    margin-bottom: 57px; }
  section#contact p {
    grid-area: p; }
  section#contact footer {
    align-self: flex-end;
    grid-area: foot; }

body {
  font-family: 'IBM Plex Sans';
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  font-size: 20px;
  color: #051429;
  text-align: left;
  line-height: normal; }

#app {
  margin: 0 33px;
  background-color: transparent;
  background-image: -webkit-linear-gradient(left, rgba(155, 155, 155, 0.2), transparent 2px);
  background-image: -o-linear-gradient(left, rgba(155, 155, 155, 0.2), transparent 2px);
  background-image: linear-gradient(90deg, rgba(155, 155, 155, 0.2), transparent 2px);
  background-size: 12.5%;
  z-index: -100; }

header {
  position: relative;
  height: 128px; }
  header div.rectangle {
    position: absolute;
    background-color: #183eef;
    top: 70px;
    z-index: 0;
    height: 50%;
    width: 100%; }
  header h1 {
    font-size: 95px;
    line-height: 1.29;
    letter-spacing: -0.5px;
    z-index: 1; }

h4 {
  font-size: 36px; }

h3 {
  font-size: 48px;
  letter-spacing: -0.2px;
  margin-bottom: 49px; }

p {
  font-weight: normal;
  opacity: 0.8;
  text-align: justify;
  color: #051429;
  line-height: 2;
  letter-spacing: -0.1px; }

a {
  font-size: 18px;
  color: #183eef; }

.pixel-container {
  position: relative; }

.pixel {
  position: absolute;
  width: 5px;
  height: 5px;
  background-color: #183eef; }

.selected {
  position: relative; }
  .selected .pixel {
    display: block; }



