section#contact {
  background: #051429; }
  section#contact a {
    color: #ffffff;
    font-size: 30px;
    opacity: 0.8; }
  section#contact div {
    z-index: 0; }
  section#contact h1 {
    color: #ffffff;
    line-height: 1.4; }
  section#contact p {
    color: #ffffff;
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
    opacity: 0.8;
    letter-spacing: -0.2px;
    font-size: 20px; }
  section#contact footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
    align-items: flex-start;
    -ms-flex-pack: justify;
    justify-content: space-between; }



